import { useEffect, useRef, useState } from 'react';
import Navi from './Navi';


//   
// React     DOM      Render  
//  (     ,  .) 
//          . 

//         Timer  Timer 
//      
//          . 



function Header(props) {
  const targetRef = useRef(null);
  const [isMobile, setisMobile] = useState(false);
  const [naviOpen, setnaviOpen] = useState(false);


  const resizeHandler = () => {
    if (window.innerWidth <= 767) {
      setisMobile(true); //isMobile  true Navi 
    } else {
      setisMobile(false); //isMobile  false Navi 
    }
    setnaviOpen(false); //    
  }


  const handleScroll = () => {
    if (window.scrollY > 100) {
      targetRef.current.classList.add('scroll');
    } else {
      targetRef.current.classList.remove('scroll')
    }
    setnaviOpen(false);
  };


  useEffect(() => {
    resizeHandler(); //   
    const timer = setInterval(() => {
      window.addEventListener("scroll", handleScroll);
      window.addEventListener('resize', resizeHandler)
    }, 100);
    console.log(targetRef.current.getBoundingClientRect(), window.scrollY)
    return () => {
      clearInterval(timer);
      window.removeEventListener("scroll", handleScroll);
      window.removeEventListener('resize', resizeHandler)
    };

    
  }, []);




  return (
    <header ref={targetRef} id="hd"
      className="App-header d-flex
                  align-items-center px-lg-5 py-3 py-md-4
                  fixed-top maincolor">
      <div className='d-flex align-items-center'>
        <h1 className="mb-0">
          <a href="#top" className="d-block ps-3">
            <img src="img/seo/logo.png" className="img-fluid w-100" alt="logo" />
          </a>
        </h1>
        <div className={naviOpen ? 'btn_toggle d-md-none position-absolute'  : 'btndiv position-absolute d-md-none'}>
          <button className='navi_btn' id="allMenu" onClick={e => {
          setnaviOpen(!naviOpen)
        }} ></button>
        </div>
      </div>
      <div className='navi d-flex align-items-center justify-content-center '>
        <Navi cls={isMobile ? 'mobileNavi ' : ''} toggle={naviOpen ? 'on' : ''}  />
      </div>
    </header>
  )
}
export default Header;